<template>
  <div class="content">
    <el-tabs v-model="activeName">

      <!--  -->
      <el-tab-pane name="first">
        <span slot="label">
          <i class="el-icon-setting"></i> 
        </span>
        <el-form :model="formEquipment"
                 label-position="left">

          <el-row>
            <el-col style="width:420px">
              <el-form-item label-width="95px"
                            label="()"
                            prop="equipmentName">
                <el-input v-model="formEquipment.equipmentName"></el-input>
              </el-form-item>
            </el-col>
            <el-col style="width:380px">
              <el-form-item label-width="55px"
                            label=""
                            prop="equipmentDescribe">
                <el-input v-model="formEquipment.equipmentDescribe"></el-input>
              </el-form-item>
            </el-col>
            <el-button style="margin:0 0 20px 20px"
                       @click="setParams"></el-button>
          </el-row>

          <el-row>
            <el-col style="width:450px">
              <el-form-item label-width="125px"
                            label="(:)"
                            prop="userParam">
                <el-input v-model="formEquipment.userParam"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

        </el-form>
        <el-collapse v-model="activeNames">
          <el-collapse-item title=""
                            name="1">
            <div>MODBUS{{formEquipment.MODBUSAdd}}</div>
            <div>{{formEquipment.searchNum}}</div>
          </el-collapse-item>
          <el-collapse-item title=""
                            name="2">
            <div>6{{formEquipment.singleRegister?'YES':"NO"}}</div>
            <div>16{{formEquipment.multiRegister?'YES':"NO"}}</div>
          </el-collapse-item>
          <el-collapse-item title=""
                            name="3">
            <div>{{formEquipment.correspondingValue}}</div>
            <div>{{formEquipment.subCorrespondingValue}}</div>
            <div>CRC{{formEquipment.doubleByteCheck?'YES':"NO"}}</div>
          </el-collapse-item>
        </el-collapse>
      </el-tab-pane>

      <!--  -->
      <el-tab-pane name="second">
        <span slot="label">
          <i class="el-icon-edit-outline"></i> 
        </span>

        <!-- table -    -->
        <equipment-tags :id="id"
                        :data-tags-org="formEquipment.dataTags"></equipment-tags>

      </el-tab-pane>

    </el-tabs>

    <!-- dialog -    -->
    <equipment-params ref="equipmentParams"
                      :id="id"
                      :form-equipment="formEquipment"></equipment-params>

  </div>
</template>

<script>
import EquipmentParams from "@/components/dialog/equipmentParams"; //  - 
import EquipmentTags from "@/components/table/equipmentTags"; //  - 

export default {
  components: { EquipmentParams, EquipmentTags },
  props: {
    // id
    id: {
      type: String
    },
    // 
    equipmentList: {
      type: Array
    }
  },
  data () {
    return {
      formEquipment: {}, // 
      activeName: "first", // tabs
      activeNames: ["1", "2", "3"] // 
    };
  },
  created () {
    this.getData();
  },
  methods: {
    // 
    getData () {
      this.equipmentList.forEach((equipment, i) => {
        equipment.id === this.id && (this.formEquipment = this.equipmentList[i]);
      });
      this.activeName = "first"; // tab
    },
    //  -  - 
    setParams () {
      this.$refs.equipmentParams.setParams();
    }
  },
  watch: {
    id (val) {
      this.getData();
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
@import '~@/styles/content.scss';
</style>



